import React from 'react';
import { Table, Tooltip, Button, Popconfirm } from 'antd';
import { EditOutlined, DeleteOutlined } from '@ant-design/icons';
import { getShortAddressString } from 'utils/string';

const ProjectTab = props => {
  const projects = props.projects;
  const tableColumns = [
    { title: '#', key: '#', render: (text, record, index) => (index + 1) },
    { title: 'Tn', dataIndex: 'name' },
    { title: 'a ch', key: 'address', render: (text, record) => getShortAddressString(record.address) },
    {
      title: 'Hnh ng', key: 'action', render: (text, record) => {
        return (
          <>
            <Tooltip title="Chnh sa">
              <Button type="link" icon={<EditOutlined />} />
            </Tooltip>
            <Tooltip title="Xa">
              <Popconfirm title="Bn c chc mun xa loi ny khng?">
                <Button danger type="link" icon={<DeleteOutlined />} />
              </Popconfirm>
            </Tooltip>
          </>
        )
      }
    }
  ]
  return (
    <Table loading={props.loading} rowKey="id" dataSource={projects} columns={tableColumns} />
  )
}

export default ProjectTab;