import React from 'react';
import { Link, useHistory } from 'react-router-dom';
import { Form, Input, Divider, Button, Row, Col, notification } from 'antd';
import AuthForm from 'containers/AuthForm/AuthForm';

import { registerAPI } from 'services/auth/register';
import { useState } from 'react';

const RegisterForm = props => {
  const history = useHistory();
  const [isLoading, setLoading] = useState(false);

  const handleRegister = async values => {
    setLoading(true);
    try {
      const registerRes = await registerAPI(values);
      notification.success({
        message: 'ng k thnh cng',
        description: 'Vui lng xc nhn ti khon ca bn bng email c gi  ng nhp.',
        duration: 2000,
        onClose: () => {
          history.replace('/dang-nhap');
        }
      })
      console.log(registerRes);
    } catch (error) {
    }
    setLoading(false);
  }

  return (
    <AuthForm>
      <h2>ng k</h2>
      <Divider />
      <Form layout="vertical" onFinish={handleRegister} noValidate>
        <Row gutter={10} style={{ flexWrap: 'nowrap' }}>
          <Col>
            <Form.Item label="H" htmlFor="first-name" name="first_name" rules={[
              { required: true, message: 'Vui lng nhp h' }
            ]}>
              <Input id="first-name" />
            </Form.Item>
          </Col>
          <Col>
            <Form.Item label="Tn" htmlFor="last-name" name="last_name" rules={[
              { required: true, message: 'Vui lng nhp tn' }
            ]}>
              <Input id="last-name" />
            </Form.Item>
          </Col>
        </Row>
        <Form.Item label="a ch email" htmlFor="email" name="email" rules={[
          { required: true, message: 'Vui lng nhp email' },
          { type: 'email', message: 'Email khng hp l' }
        ]}>
          <Input id="email" type="email" />
        </Form.Item>
        <Form.Item label="Mt khu" htmlFor="password" name="password" rules={[
          { required: true, message: 'Vui lng nhp mt khu' },
        ]} hasFeedback>
          <Input.Password id="password" />
        </Form.Item>
        <Form.Item label="Nhp li mt khu" htmlFor="confirm-password" name="confirmPassword" rules={[
          { required: true, message: 'Vui lng nhp li mt khu' },
          ({ getFieldValue }) => ({
            validator(rule, value) {
              if (!value || getFieldValue('password') === value) {
                return Promise.resolve();
              }
              return Promise.reject('Mt khu khng khp nhau');
            }
          })
        ]} hasFeedback>
          <Input.Password id="confirm-password" />
        </Form.Item>
        {/*
        <Form.Item label="in thoi" htmlFor="phone" name="phone" rules={[
          { required: true, message: 'Vui lng nhp s in thoi' },
          { pattern: phoneValidationString, message: 'S in thoi khng hp l' }
        ]}>
          <Input id="phone" />
        </Form.Item>
        */}
        <Form.Item >
          <Button type="primary" loading={isLoading} htmlType="submit" block>ng k</Button>
        </Form.Item>
        <p className="text-center"> c ti khon? <Link to="/dang-nhap">ng nhp</Link></p>
      </Form>
    </AuthForm>
  )
}

export default RegisterForm;