import React from 'react';
import { Row, Col, Form, Select, Button, Input } from 'antd';

const { Option } = Select;

const SearchForm = props => {
  return (
    <Row justify="center">
      <Col sm={24} md={22} lg={18} xl={14} xxl={12}>
        <Form layout="vertical">
          <Row gutter={8}>
            <Col>
              <Form.Item>
                <Select defaultValue="1">
                  <Option value="1">Nh</Option>
                  <Option value="2">t</Option>
                </Select>
              </Form.Item>
            </Col>
            <Col flex={1}>
              <Form.Item>
                <Input placeholder="Nhp t kha tm kim" />
              </Form.Item>
            </Col>
            <Col>
              <Form.Item>
                <Button type="primary">Tm kim</Button>
              </Form.Item>
            </Col>
          </Row>
          <Row gutter="8">
            <Col flex="1">
              <Form.Item label="Tnh/thnh ph">
                <Select defaultValue="1">
                  <Option value="1">Cn Th</Option>
                </Select>
              </Form.Item>
            </Col>
            <Col flex="1">
              <Form.Item label="Qun/huyn">
                <Select defaultValue="1">
                  <Option value="1">Ninh Kiu</Option>
                </Select>
              </Form.Item>
            </Col>

            <Col flex="1">
              <Form.Item label="Phng ng">
                <Input type="number" defaultValue="1" />
              </Form.Item>
            </Col>
            <Col flex="1">
              <Form.Item label="Phng tm">
                <Input type="number" defaultValue="1" />
              </Form.Item>
            </Col>
          </Row>
          <Row gutter={8} align="bottom" justify="center">
            <Col flex="0 0 150px">
              <Form.Item label="Gi">
                <Input type="number" defaultValue={0} min={0} />
              </Form.Item>
            </Col>
            <Col flex="0 0 150px">
              <Form.Item>
                <Input type="number" defaultValue={0} min={0} />
              </Form.Item>
            </Col>
            <Col flex="0 0 100px">
              <Form.Item label="Din tch">
                <Input type="number" defaultValue={0} min={0} />
              </Form.Item>
            </Col>
            <Col flex="0 0 100px">
              <Form.Item>
                <Input type="number" defaultValue={0} min={0} />
              </Form.Item>
            </Col>
            <Col flex="0 0 150px">
              <Form.Item label="Nm xy dng">
                <Input type="number" defaultValue={new Date().getFullYear()} min={0} />
              </Form.Item>
            </Col>
          </Row>
        </Form>
      </Col>
    </Row>
  )
}

export default SearchForm;